<template>
  <div>
    <van-nav-bar
      title="我的收藏/历史/作品"
      left-text="返回"

      @click-left="$router.back()"

    />
    <van-tabs v-model="active">
      <van-tab title="我的收藏">
          <my-art :type='0'></my-art>
      </van-tab>
      <van-tab title="我的历史">
          <my-art :type='1'></my-art>
      </van-tab>
      <van-tab title="我的作品">
          <my-art :type='2'></my-art>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import myart from './myart'
export default {
  data () {
    return {
      active: 0
    }
  },
  components: {
    'my-art': myart
  },
  props: ['type'],
  created () {
    // console.log(this.type)
    this.active = this.type === undefined ? 2 : { 'shoucang': 0, 'history': 1 }[this.type]
  }
}
</script>

<style lang='less' scoped>
::v-deep .van-nav-bar__text{
    color: #fff;
}
</style>
